<template>
  <div class="hello">
    <div class="wrap">
      <div class="left">
        <a href="">网易云音乐</a>
      </div>
      <ul>
        <li v-for="(val,index) in msg"
            :key="index">{{val}}</li>
      </ul>
    </div>
    <div>显示</div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: ['我的', '下载', '上次', '更多的', '对对对'],
      a: 'wode'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



